/* eslint-disable */
Drupal.behaviors.mainNav = {
    attach(context) {
        const btnLi = context.getElementsByClassName(
            'main-menu__list__item main-menu__list__item--with-sub',
        );
        const btnA = context.getElementsByClassName(
            'main-menu__list__link main-menu__list__link--with-sub',
        );
        const contentLi = context.getElementsByClassName(
            'main-menu__list main-menu__list--sub main-menu__list--sub-1',
        );
        const mainNav = context.querySelector('.main-menu__nav');
        const btnToggle = context.querySelector('.main-menu__toggle');
        const arrBtnLi = [...btnLi];
        const arrContentLi = [...contentLi];

        arrContentLi.forEach((element, index) => {
            if (screen.width < 1024) {
                btnA[index]?.addEventListener('click', function () {
                    if (
                        element.style.display === '' ||
                        element.style.display === 'none'
                    ) {
                        element.style.display = 'flex';
                        console.log('click 2', element.style.display);
                    } else {
                        element.style.display = 'none';
                        console.log('click', element.style.display);
                    }
                });
            } else {
                arrBtnLi[index]?.addEventListener('mouseover', function () {
                    if (
                        element.style.display === '' ||
                        element.style.display === 'none'
                    ) {
                        element.style.display = 'flex';
                    }
                });

                arrBtnLi[index]?.addEventListener('mouseleave', function () {
                    if (element.style.display === 'flex') {
                        element.style.display = 'none';
                    }
                });

                arrContentLi[index]?.addEventListener('mouseleave', function () {
                    if (element.style.display === 'flex') {
                        element.style.display = 'none';
                    }
                });
            }
        });

        // Logica para mobile
        btnToggle.addEventListener('click', function () {
            if (mainNav.style.display === '' || mainNav.style.display === 'none') {
                mainNav.style.display = 'flex';
            } else {
                mainNav.style.display = 'none';
            }
        });
    },
};
